  <div class="container">
    <ul class="nav nav-tabs">
      <li class="nav-item">
        <a class="nav-link active" data-toggle="tab" href="#home">Add Item</a>
      </li>
      <!-- <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu1">User Info</a>
      </li>-->
      <!-- <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu2">Report</a>
      </li>-->
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu3">Add Combo</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu4">Discount</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu5">Discount all</a>
      </li>
    </ul>

    <!-- Tab panes -->
    <div class="tab-content">
      <div id="menu5" class="container tab-pane fade">
        <h5>Discount all:</h5>
        <ul class="list-group">
          <li
            class="list-group-item list-group-item-action list-group-item-light"
          >Discount: {{discountedAll.discount_percent + '%' || 0 + '%'}} ~ {{discountedAll.discount_value || 0}} --- Time: {{cvDate(discountedAll)}}</li>
        </ul>
        <div class="form-group">
          <label class="col-md-4 control-label">Start Time</label>
          <div class="col-md-4">
            <input class="form-control" type="date" v-model="discountAll.start_time">
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label">End Time</label>
          <div class="col-md-4">
            <input class="form-control" type="date" v-model="discountAll.end_time">
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label">Discount percent</label>
          <div class="col-md-4">
            <input
              class="form-control"
              type="number"
              v-model="discountAll.discount_percent"
              min="0"
              max="100"
            >
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label">Discount value</label>
          <div class="col-md-4">
            <input class="form-control" type="number" v-model="discountAll.discount_value" min="0">
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label" for="singlebutton"></label>
          <div class="col-md-4">
            <button
              id="singlebutton"
              name="singlebutton"
              class="btn btn-success"
              @click="addDiscountAll()"
            >Add discount</button>
          </div>
        </div>

      </div>
      <div id="menu4" class="container tab-pane fade">
        <div class="form-group">
          <label class="col-md-4 control-label">Item id</label>
          <div class="col-md-4">
            <select class="form-control" v-model="selectedItem" @change="fetchItemDiscount()">
              <option
                v-for="item in items"
                :value="item"
                :key="item.id"
              >{{ item.name }} - Price: {{item.price}}</option>
            </select>
          </div>
        </div>
        <div class="form-group" v-if="itemDiscounts.length">
          <h5>Discount list:</h5>
          <ul class="list-group" v-for="(i, index) in itemDiscounts" :key="index">
            <li
              class="list-group-item list-group-item-action list-group-item-light"
            >Discount: {{i.discount_percent + '%' || 0 + '%'}} ~ {{i.discount_value || 0}} --- Time: {{cvDate(i)}}</li>
          </ul>
        </div>
        <div v-if="selectedItem">
          <div class="form-group">
            <label class="col-md-4 control-label">Start Time</label>
            <div class="col-md-4">
              <input class="form-control" type="date" v-model="discount.start_time">
            </div>
          </div>

          <div class="form-group">
            <label class="col-md-4 control-label">End Time</label>
            <div class="col-md-4">
              <input class="form-control" type="date" v-model="discount.end_time">
            </div>
          </div>

          <div class="form-group">
            <label class="col-md-4 control-label">Discount percent</label>
            <div class="col-md-4">
              <input
                class="form-control"
                type="number"
                v-model="discount.percent"
                min="0"
                max="100"
              >
            </div>
          </div>

          <div class="form-group">
            <label class="col-md-4 control-label">Discount value</label>
            <div class="col-md-4">
              <input
                class="form-control"
                type="number"
                v-model="discount.value"
                min="0"
                :max="selectedItem.price"
              >
            </div>
          </div>

          <div class="form-group">
            <label class="col-md-4 control-label" for="singlebutton"></label>
            <div class="col-md-4">
              <button
                id="singlebutton"
                name="singlebutton"
                class="btn btn-success"
                @click="addDiscount()"
              >Add discount</button>
            </div>
          </div>
        </div>
      </div>

      <div id="home" class="container tab-pane active">
        <br>

        <!-- Text input-->
        <div class="form-group">
          <label class="col-md-4 control-label">Product name</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="text"
              placeholder
              class="form-control input-md"
              v-model="item.name"
            >
          </div>
        </div>

        <!-- Text input-->
        <div class="form-group">
          <label class="col-md-4 control-label">Price - unit</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="number"
              placeholder
              class="form-control input-md"
              v-model="item.price"
            >
          </div>
        </div>

        <!-- Text input-->
        <div class="form-group">
          <label class="col-md-4 control-label">stock</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="number"
              placeholder
              class="form-control input-md"
              v-model="item.stock"
            >
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label">Description</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="text"
              placeholder
              class="form-control input-md"
              v-model="item.desc"
            >
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label" for="singlebutton"></label>
          <div class="col-md-4">
            <button id="singlebutton" name="singlebutton" class="btn btn-success" @click="add()">add</button>
          </div>
        </div>
      </div>
      <div id="menu1" class="container tab-pane fade">
        <br>
        <!-- tab2 -->
        <ul class="list-group">
          <li class="list-group-item" v-for="user in users" :key="user.id">{{user.name}}</li>
        </ul>
      </div>
      <div id="menu2" class="container tab-pane fade">
        <br>
        <!-- tab3 -->
        <table class="table">
          <thead class="thead-dark">
            <tr>
              <th scope="col">#</th>
              <th scope="col">First</th>
              <th scope="col">Last</th>
              <th scope="col">Handle</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(report, index) in reports" :key="index">
              <th scope="row">{{index}}</th>
              <td>{{report.name}}</td>
              <td>{{report.title}}</td>
              <td>{{report.price}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="container tab-pane fade" id="menu3">
        <br>

        <div class="form-group">
          <label class="col-md-4 control-label">Name</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="text"
              placeholder
              class="form-control input-md"
              v-model="combo.name"
            >
          </div>
        </div>
        <div class="form-group">
          <label class="col-md-4 control-label">Price</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="number"
              placeholder
              class="form-control input-md"
              v-model="combo.price"
            >
          </div>
        </div>
        <!-- <div class="form-group">
          <label class="col-md-4 control-label">stock</label>
          <div class="col-md-4">
            <input
              id="textinput"
              name="textinput"
              type="text"
              placeholder
              class="form-control input-md"
              v-model="combo.stock"
            >
          </div>
        </div>-->
        <div class="form-group">
          <label class="col-md-4 control-label">Description</label>
          <div class="col-md-4">
            <textarea cols="38" rows="5" v-model="combo.desc"></textarea>
          </div>
        </div>
        <div class="form-group">
          <label class="col-md-4 control-label">Items</label>
          <div class="col-md-4">
            <select v-model="combo.items" class="form-control custom-select" multiple>
              <option
                :value="item.id"
                v-for="(item, index) in items"
                :key="index"
              >{{item.name}} - Price: {{item.price}} - Stock: {{item.stock}}</option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <label class="col-md-4 control-label" for="singlebutton"></label>
          <div class="col-md-4">
            <button
              id="singlebutton"
              name="singlebutton"
              class="btn btn-success"
              @click="addCombo()"
            >add</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import saleManage from "../modules/saleManage";
import u from "../modules/util";

import db from "../configs/firebase.config";
import Combo from "../models/combo.model";
import Item from "../models/item.model";
import Discount from "../models/discount.model";

export default {
  name: "sale",
  data() {
    return {
      selectedItem: "",
      item: {
        name: "",
        price: "",
        stock: "",
        discount_percent: "",
        discount_value: ""
      },
      users: [],
      reports: [],
      items: "",
      combo: {
        name: "",
        desc: "",
        price: "",
        items: []
      },
      discount: {
        start_time: null,
        end_time: null,
        percent: null,
        value: null,
        item_id: null
      },
      discountAll: {
        start_time: new Date().toISOString().substr(0, 10),
        end_time: null,
        discount_percent: null,
        discount_value: null
      },
      itemDiscounts: [],
      discountedAll: {}
    };
  },
  methods: {
    addDiscountAll() {
      this.discountAll.start_time = new Date(this.discountAll.start_time).getTime()
      this.discountAll.end_time = new Date(this.discountAll.end_time).getTime()
      u.setCollection('discountAll').create(this.discountAll, doc => console.log('discount all added'))
    },
    addDiscount() {
      this.discount.item_id = this.selectedItem.id;
      let a = new Discount(this.discount);
      console.log(a);
      u.setCollection("discount").create(a, doc => console.log(doc));
      this.reset();
    },
    fetchItemDiscount() {
      u.setCollection("discount").getWithCondition(
        [{ field: "item_id", op: "==", value: this.selectedItem.id }],
        doc => {
          if (doc != "None") this.itemDiscounts = doc;
        }
      );
    },
    cvDate(i) {
      return i.end_time < Date.now()
        ? "Ended"
        : new Date(i.start_time).toLocaleDateString() +
            " - " +
            new Date(i.end_time).toLocaleDateString();
    },
    add() {
      let a = new Item(this.item);
      u.setFirebaseDB(db)
        .setCollection("items")
        .create(a, doc => console.log(doc), err => console.log(err));
      this.reset();
    },
    reset() {
      this.item = {
        name: "",
        price: "",
        stock: "",
        discount_percent: "",
        discount_value: ""
      };
      this.combo = {
        name: "",
        stock: "",
        price: "",
        items: [],
        discount_percent: "",
        discount_value: ""
      };
      this.discount = {
        start_time: null,
        end_time: null,
        percent: null,
        value: null,
        item_id: null
      };
      this.selectedItem = "";
    },
    addCombo() {
      let a = new Combo(this.combo);
      console.log(a);
      u.setCollection("combo").create(
        a,
        doc => console.log(doc),
        err => console.log(err)
      );
      this.reset();
    }
  },
  created() {
    u.setFirebaseDB(db);
    u.setCollection("items")
      .setOrderBy("created_time")
      .getAllRealTime(doc => {
        this.items = doc;
        console.log(doc);
      });
    u.setCollection('discountAll').getWithCondition([{field: 'end_time', op: '>', value: Date.now()}], doc => {
      if (doc) {
        // if (doc[0].start_time < Date.now())
        this.discountedAll = doc[0]
      }
    })
  },
  mounted() {
    // saleManage
    //   .setFirebaseDB(db)
    //   .setCollection("reports")
    //   .setOrderBy("createdTime")
    //   .setTimeRange(new Date(Date.now() - 86400000), Date.now())
    //   .getWithOrderBy("id", 1, doc => this.reports = doc);
    fetch("https://jsonplaceholder.typicode.com/users")
      .then(response => response.json())
      .then(json => (this.users = json));
  }
};
</script>

<style>
</style>



