import {randomID} from '../utilities';

class Item {
  constructor(a) {
    this.id = randomID();
    this.name = a.name;
    this.stock = +a.stock;
    this.price = +a.price;
    this.desc = a.desc;
    // this.discount_percent = +a.discount_percent > 100 ? 100 : +a.discount_percent;
    // this.discount_value = +a.discount_value > this.price ? this.price : +a.discount_value
    // this.final_price = (+(this.price - this.discount_value) * (100 - this.discount_percent) / 100);
    this.created_time = Date.now();
    this.type = 'item'
  }
}
export default Item;



