import {
  randomID
} from '../utilities';

class Discount {
  constructor(a) {
    this.id = randomID();
    this.start_time = new Date(a.start_time).getTime();
    this.end_time = new Date(a.end_time).getTime();
    this.item_id = a.item_id;
    this.discount_percent = +a.percent;
    this.discount_value = +a.value;
  }
}

export default Discount;



