import {
  randomID
} from '../utilities'

class Combo {
  constructor(a) {
    this.id = randomID()
    this.name = a.name
    this.items = a.items // [id, id,..]
    this.created_time = Date.now()
    this.price = +a.price
    // this.stock = +a.stock
    this.discount_percent = +a.discount_percent > 100 ? 100 : +a.discount_percent || 0 
    this.discount_value = +a.discount_value > this.price ? this.price : +a.discount_value || 0
    // this.final_price = (this.price - this.discount_value) * (100 - this.discount_percent) / 100;
    this.desc = a.desc
    this.type = 'combo'
  }
}

export default Combo



